import { Col } from "reactstrap";
import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  CardSubtitle,
  Button,
} from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import { faVideo } from "@fortawesome/free-solid-svg-icons";
import { faStar } from "@fortawesome/free-solid-svg-icons";

export function Movie(props) {
    return(
        <Col xs="12" lg="6" xl="4" className="mb-4">
          <Card>
            <CardImg top width="100%" src={props.movieImg} alt="Card image cap" />
            <CardBody>
              <CardText>
                Like{' '}
                <FontAwesomeIcon icon={faHeart} style={{cursor:'pointer'}} />
              </CardText>
              <CardText>
                Nombre de vues{' '} 
                <FontAwesomeIcon icon={faVideo} />
              </CardText>
              <CardText>
                Mon avis{' '}
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />{' '}
                <Button>-1</Button>
                <Button>+1</Button>
              </CardText>
              <CardText>
                Avis Global{' '}
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />
                <FontAwesomeIcon icon={faStar} />{' '}
              </CardText>
              <CardText>{props.movieName}</CardText>
              <CardText>{props.movieDesc}</CardText>
            </CardBody>
          </Card>
        </Col>
    )
}